//!-----Variables-----

$text-color: #FFFFFF;
$link-color: #018ABE;
$link-second-color: #FFFFFF;

$button-color: #FFFFFF;
$button-back-color: #018ABE;


$background-color: #162831;
$background-menu-color: #0f1e25ee;
$blocks-color: #018ABE;

$title-icon-color: #018ABE;

$control-menu-blue: #018ABE;
$control-menu-white: #FFFFFF;

//!-----/Variables-----

//?-----Box Model-----


*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

//?-----/Box Model-----

//!-----Page-----

.page {
  line-height: 1.5;
  font-family: "Dewi", sans-serif;
  color: $text-color;

  // .page__body

  &__body {
    background-color: $background-color;
    font-size: 16px;
  }
}

//!-----/Page-----

//?-----Container-----
.container {
  max-width: 1298px;
  margin: 0 auto;
}

//?-----/container-----

//!-----flipBox-----

.flip-box {
  perspective: 1000px;
  //width: 300px;
  //height: 300px;

  &:hover &__inner {
    transform: rotateX(180deg);
  }

  // .flip-box__inner

  &__inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  // .flip-box__front, .flip-box__back
  &__front,
  &__back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;



  }

  // .flip-box__front

  &__front {
    background-color: #bbb;
    color: $text-color;
  }

  // .flip-box__back

  &__back {
    color: $text-color;
    transform: rotateX(180deg);

    &::before {
      left: 0;
    }
  }


}

//!-----flipBox-----

//*--Button

.button {
  padding: 9px 15px;
  border-radius: 5px;
  background-color: $button-back-color;
  color: $button-color;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.4em;
  cursor: pointer;
}

.button:hover {
  opacity: .8;
}

//*--/Button

//?-----Header-----


.header {
  display: grid;
  padding: 16px;
}



@media (width >736px) {

  .header {
    grid-template-columns: max-content max-content;
    justify-content: space-between;
  }

  .header-menu {
    align-self: center;
  }

}

@media (width <=736px) {
  .header__logo {
    z-index: 1;
  }

  .header-menu {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }
}

//?---Logo

.logo {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 8px;
  align-items: center;

  // .logo__image

  &__image {}

  // .logo__title

  &__title {
    margin: 0;
    font-weight: bold;
    font-size: 20px;
    letter-spacing: -0.06em;
  }
}


//?---Menu

.menu {

  // .menu__list

  &__list {
    list-style: none;
  }

  // .menu__item

  &__menu__item {
    letter-spacing: 0.02em;
    text-transform: uppercase;
  }

  // .menu__link

  &__link {
    color: $link-color;
    text-decoration: none;
    font-size: 18px;
    cursor: pointer;

    &:hover {
      opacity: .9;
      color: $link-second-color;
    }
  }

  // .menu__link--current

  &__link--current {
    color: $link-second-color;
    font-weight: bold;
    font-family: 'Dewi Expanded', sans-serif;
  }
}

@media(width <=736px) {
  .menu {
    display: grid;
    background-color: $background-menu-color;

    // .menu__button
    &__button {
      z-index: 1;
      display: block;
      margin-top: 24px;
      margin-right: 16px;
      padding-bottom: 20px;
      border: none;
      background-color: transparent;
      justify-self: end;
    }

    // .menu__icon

    &__icon {
      stroke: $control-menu-blue;

      &:hover {
        stroke: $control-menu-white;
      }
    }

    // .menu__icon-menu

    &__icon-menu,
    &__button--open &__icon-cross {
      display: block;
    }

    // .menu__icon-cross

    &__icon-cross,
    &__button--open &__icon-menu {
      display: none;
    }

    // .menu__list

    &__list {
      display: none;
      z-index: 2;
      row-gap: 8px;
      justify-items: center;
      padding-top: 12px;
      padding-bottom: 24px;
      background-color: $background-menu-color;
      box-shadow: 0 50vh 0 50vh $background-menu-color;
    }

    &__list--open {
      display: grid;
    }
  }
}

@media (width > 736px) {
  .menu__button {
    display: none;
  }

  .menu__list {
    display: grid;
    grid-auto-flow: column;
    column-gap: 20px;
  }

}

//?-----/Header-----

//!-----header-short-----


.header-short {
  display: grid;
  justify-items: center;
  align-items: center;
  overflow: auto;
  margin-bottom: 75px;

  // .header-short__image

  &__image {
    position: relative;
    opacity: .6;
  }
}


.header-info {
  position: absolute;
  z-index: 1;
  display: grid;
  justify-items: center;
  padding: 0 10px;

  // .header-info__title

  &__title {
    line-height: 1.4em;
  }

  // .header-info__descr

  &__descr {
    line-height: 1.4em;
    margin-bottom: 20px;
  }

  // .header-info__button

  &__button {}
}

@media (width >=320px) {

  .header-info__title {
    font-size: 16px;
    text-align: center;


  }

  .header-info__descr {
    font-size: 12px;
  }
}

@media (width >564px) {

  .header-info__title {
    font-size: 24px;
  }

  .header-info__descr {
    font-size: 16px;
  }
}

@media (width >879px) {

  .header-info__title {
    font-size: 38px;
  }

  .header-info__descr {
    font-size: 24px;
  }
}

//!-----/header-short-----

//?-----service-----
.service {
  text-align: center;
  margin-bottom: 75px;

  // .service__set-cards

  &__set-cards {}
}

.title {
  margin-bottom: 75px;
  text-align: center;
}

@media (width <=736px) {
  .title {
    margin-bottom: 25px;
  }

}


.service-card {

  // .service-card__title

  &__title {}

  @media (width <=736px) {

    &__title {
      font-weight: 600;
      font-size: 18px;
      line-height: 1em;
      text-align: center;
    }

  }

  // .service-card__description

  &__description {}


}

.set-cards {
  display: grid;
  justify-items: center;
  gap: 15px;
  grid-auto-flow: row;


  &__card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 250px;
    position: relative;
    min-height: 200px;
    border-radius: 5px;
    text-align: center;

    &:hover {
      box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    }
  }
}

@media (width >550px) {

  .set-cards {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    grid-template-areas:
      ". ."
      ". ."
      ". .";
  }
}

@media (width >806px) {

  .set-cards {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
      ". . ."
      ". . .";
  }
}

// .set-cards__card

.card {

  // .card__content

  &__content {
    position: absolute;
    bottom: 0;
    border: 1px solid $text-color;
    border-radius: 5px;
    background: rgb(3, 89, 122);
    background: rgba(2, 42, 58, 0.7);
    color: #f1f1f1;
    width: 100%;
    height: 100%;
    padding: 20px;
    overflow: auto;
  }

  // .card__title

  &__title {
    font-size: 18px;
    border-bottom: 4px dotted $blocks-color;
  }

  // .card__description

  &__description {
    font-size: 14px;
  }

  // .card__image

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    border-radius: 5px;
  }
}

//?-----/service-----

//!-----algorithm-----

.algorithm {
  padding: 10px;
  margin-bottom: 75px;

  // .algorithm__title

  &__title {}

  // .algorithm__section

  &__section {
    overflow: auto;

    margin-bottom: 8px;

  }
}

.section {
  min-width: 300px;
  min-height: 75px;
  text-align: center;
  padding: 5px 10px;

  background-color: $blocks-color;
  border: 1px solid $text-color;
  border-radius: 5px;

  &::before {
    content: '';
    border: solid $text-color;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;

    width: 3px;
    height: 3px;

    transform: rotate(45deg);

    position: absolute;
    top: 10px;
    left: 5px;
  }

  // .section__title

  &__title {}

  // .section__desc

  &__desc {}
}

//!-----/algorithm-----

//?-----Price-----

.price {

  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  // .price__title

  &__title {}

  // .price__price-column

  &__price-column {}

  // .price__image

  &__image {
    margin: 0 auto;
  }
}



.price-column {
  padding: 10px;
  max-height: 275px;

  // .price-column__price-row

  &__price-row {
    display: flex;
    text-align: center;
    flex-wrap: wrap;
    justify-content: center;
    padding: 5px;


    &:not(:last-child) {
      border-bottom: 6px dotted $blocks-color;
    }
  }
}

.price-row {



  // .price-row__title

  &__title {
    width: 50%;
  }

  // .price-row__price

  &__price {
    width: 50%;
  }

  // .price-row__button

  &__button {
    margin-top: 20px;
  }
}







//?-----/Price-----

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, .8);
  padding-top: 60px;

  // .modal__modal-content

  &__modal-content {
    background-color: $background-color;
    margin: 5% auto 15% auto;
    border: 1px solid #888;
    width: 80%;
  }

  // .modal__img-container

  &__img-container {
    text-align: center;
    margin: 12px 0;
    position: relative;
  }

  // .modal__container

  &__container {
    padding: 16px;
    background-color: $background-color;
  }

  // .modal__cancelbtn

  &__cancelbtn {
    width: auto;
    padding: 10px 18px;
    background-color: #f44336;
  }

  //.modal__name

  &__name,
  &__number {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid $blocks-color;
  }

  //.modal__number

  &__number {}
}

/* Add Zoom Animation */
.animate {
  animation: animatezoom 0.6s
}

.close {
  position: absolute;
  right: 25px;
  top: 0;
  color: $button-color;
  font-size: 35px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: $control-menu-blue;
  cursor: pointer;
}


@-webkit-keyframes animatezoom {
  from {
    -webkit-transform: scale(0)
  }

  to {
    -webkit-transform: scale(1)
  }
}

@keyframes animatezoom {
  from {
    transform: scale(0)
  }

  to {
    transform: scale(1)
  }
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 300px) {
  span.psw {
    display: block;
    float: none;
  }

  .cancelbtn {
    width: 100%;
  }
}

@media (width >=320px) {

  .price__img {
    display: none;
  }
}

@media (width>=740px) {

  .price {

    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    // .price__title

    &__title {
      width: 100%;
    }

    // .price__price-column

    &__price-column {
      width: 50%;
    }

    // .price__image

    &__image {
      width: 50%;
      max-height: 275px;
      overflow: hidden;
      position: relative;
    }
  }

  .price__img {
    display: block;
    position: relative;
    filter: grayscale(100%);

  }

  .price__button {
    position: absolute;
    right: 33.333%;
    bottom: 33.333%;
  }

  .modal__modal-content {
    width: 50%;
  }
}